import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import styles from "../styles/NavBar.module.css"
import cx from "classnames"

function NavBar({ style }) {
  const [toggle, setToggle] = useState(window.innerWidth > 768 ? false : true)
  const [dropdown, setDropdown] = useState(false)

  useEffect(() => {
    const SIZE = () => {
      console.log("REsize", window.innerWidth)
      if (window.innerWidth < 768) {
        console.log("SMALL")
        setToggle(true)
      } else {
        setToggle(false)
        console.log("BIG")
      }
    }
    window.addEventListener("resize", SIZE)
    return () => {
      window.removeEventListener("resize",SIZE)
    }
  }, [])
  const NavItems = () => {
    return (
      <ul className={styles.navright}>
        <li>
          <Link to="/contact/">
            <p>contact</p>
          </Link>
        </li>
        <li>
          <Link to="/blog/">
            <p>blog</p>
          </Link>
        </li>
        <li>
          <Link to="/gallery/">
            <p>gallery</p>
          </Link>
        </li>
      </ul>
    )
  }

  return (
    <div style={{ ...style }} className={styles.container}>
      <div className={dropdown ? styles.dropdown : styles.hideDropdown}></div>
      <nav className={styles.nav}>
        <Link className={styles.logo} to="/">
          <h1>ANEES</h1>
        </Link>
        {toggle ? (
          <div
            className={dropdown ? styles.closeIcon : styles.toggleIcon}
            onClick={() => setDropdown(state => !state)}
          >
            <div className={cx(styles.bar, styles.bar1)}></div>
            <div className={cx(styles.bar, styles.bar2)}></div>
            <div className={cx(styles.bar, styles.bar3)}></div>
          </div>
        ) : (
          <NavItems />
        )}
      </nav>
    </div>
  )
}

export default NavBar
